body {
  background-color: #d2691e;
  justify-content: center;
  display: flex;
  overflow: hidden;
}

.board {
  grid-template-columns: repeat(var(--N), min(11.75vh, 11.75vw));
  grid-template-rows: repeat(var(--M), min(11.75vh, 11.75vw));
  gap: min(.5vh, .5vw);
  display: grid;
}

.field {
  background-color: #f0d9b5;
}

.field.black {
  background-color: #946037;
}

.field.white {
  background-color: #e9c181;
}

.field.white.selected {
  background-color: #e5ab4e;
}

.field.black.selected {
  background-color: #ce681b;
}

.field.black.possible {
  background-color: #9acd32;
}

.ampel {
  z-index: -1;
  width: min(10vw, 10vh);
  height: min(10vw, 10vh);
  position: absolute;
  bottom: max(-5vw, -5vh);
  right: max(-5vw, -5vh);
}
